<template>
    <yd-layout>
        <div class="setTop">
            <yd-flexbox>
                <yd-flexbox-item class="active"><span></span>提醒设置</yd-flexbox-item>
                <yd-flexbox-item>
                    <router-link to="/customForm"><span></span>报表制作</router-link>
                </yd-flexbox-item>
                <yd-flexbox-item>
                    <router-link to="/appCenter"><span></span>应用中心</router-link>
                </yd-flexbox-item>
            </yd-flexbox>
        </div>
        <div class="remindBtn">
            <div class="remindList">
                <div class="remindLeft">代办提醒</div>
                <div class="remindRight">
                    <yd-switch v-model="switch1" color="#59a5fd"></yd-switch>
                </div>
            </div>
            <div class="remindList">
                <div class="remindLeft">会议提醒</div>
                <div class="remindRight">
                    <yd-switch v-model="switch2" color="#59a5fd"></yd-switch>
                </div>
            </div>
            <div class="remindList">
                <div class="remindLeft">日程提醒</div>
                <div class="remindRight">
                    <yd-switch v-model="switch3" color="#59a5fd"></yd-switch>
                </div>
            </div>
            <div class="remindList">
                <div class="remindLeft">参会确认提醒</div>
                <div class="remindRight">
                    <yd-switch v-model="switch4" color="#59a5fd"></yd-switch>
                </div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
export default {
    data() {
        return {
            switch1: true,
            switch2: true,
            switch3: true,
            switch4: true
        }
    }
}
</script>
<style lang="less" scoped>
.setTop {
    margin-top: 0;
    height: 1.1rem;
    background-color: #fff;
    .yd-flexbox {
        line-height: 1.1rem;
        font-size: .34rem;
        color: #999;
        .yd-flexbox-item {
            text-align: center;
            position: relative;
        }
        .active {
            color: #333;
            span {
                display: block;
                height: .06rem;
                width: 1.64rem;
                border-radius: 2px;
                background-color: #59a5fd;
                position: absolute;
                bottom: -0.03rem;
                left: .42rem;
            }
        }
    }
}

.remindBtn {
    font-size: .34rem;
    background-color: #fff;
    color: #333;
    border-top: .26rem solid #f8f8f8;
    .remindList {
        height: 1.13rem;
        // line-height: 1.13rem;
        padding-left: .54rem;
        padding-right: .44rem;
        border-bottom: 1px solid #eaeaea;
        .remindLeft {
            float: left;
            margin-top: .35rem;
        }
        .remindRight {
            float: right;
            margin-top: .3rem;
        }
    }
}

.yd-switch {
    height: .54rem;
    width: 1.22rem;
}

.yd-switch:before {
    background-color: #dfdfdf;
    height: .45rem;
    width: .45rem;
}

.yd-switch:after {
    height: .45rem;
    width: .45rem; // left: .35rem;
    top: .02rem;
}

.yd-switch:checked:after {
    -webkit-transform: translateX(.73rem);
    transform: translateX(.73rem);
}
</style>


